.features {
  height: 310px;

  .container {
    height: 100%;
  }
}

  .features-container {
    position: absolute;
    bottom: 0;
    height: 390px;
    width: 100%;
    font-size: 0;
  }

    .features-item {
      height: 340px;
      background-color: $blue3;
      width: 25%;
      display: inline-block;
      transition: all .5s;
      position: absolute;
      bottom: 0;
      color: #fff;
      padding: 30px;
      overflow: hidden;

      &.features-item_2 {
        left: 25%;
        background-color: $blue2;
      }

      &.features-item_3 {
        left: 50%;
        background-color: $blue1;
      }

      &.features-item_4 {
        left: 75%;
        background-color: $blue;
      }

      &:hover {
        height: 390px;

        .features-item__more-button {
          right: 30px;
        }
      }
    }

      .features-item__logo {
        font-size: 62px;
        line-height: 62px;
      }

      .features-item__title {
        font-size: 24px;
        line-height: 38px;
        white-space: pre-line;
        margin: 15px 0 5px;
      }

      .features-item__text {
        font-size: 14px;
      }

      .features-item__more-button {
        display: inline-block;
        position: absolute;
        bottom: 30px;
        right: -30px;
        font-size: 28px;
        width: 28px;
        text-align: center;
        color: #fff;
        transition: all .5s;
      }

.more-features {

  .container {
    border-bottom: 3px solid $blue;
  }
}

  .more-features__block {
    margin-left: -30px;
    font-size: 0;
  }

    .more-features__item-container {
      display: inline-block;
      height: 200px;
      width: calc(50% - 30px);
      margin-left: 30px;
      padding: 50px 0;
      border-top: 1px solid rgba($black, .1);

      &:hover {

        .more-features__item-icon {
          background-color: $blue3;
        }

        .more-features__item-title {
          color: $blue;
        }
      }

      &:first-child,
      &:first-child + div {
        border-top: none;
      }
    }

      .more-features__item-icon {
        width: 100px;
        height: 100px;
        background-color: $blue;
        text-align: center;
        line-height: 100px;
        display: inline-block;
        color: #fff;
        margin-right: 30px;
        vertical-align: middle;
        font-size: 52px;
        transition: all .5s;
      }

      .more-features__item-info {
        display: inline-block;
        width: 430px;
        vertical-align: middle;
        overflow: hidden;
      }

        .more-features__item-title {
          color: $black;
          font-size: 24px;
          transition: all .5s;
        }

        .more-features__item-text {
          color: $gray;
          font-size: 14px;
        }

.info {
  margin: 50px 0;

  .button {
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

  .info-container {
    font-size: 0;
    margin-left: -30px
  }

    .info-col {
      width: calc(33% - 30px);
      display: inline-block;
      margin-left: 30px;
      height: 655px;
      vertical-align: top;
      position: relative;
    }

      .info-col__title {
        font-size: 34px;
        line-height: 38px;
        color: $black;
        margin-bottom: 25px;
      }

      .info-col__pic {
        margin: 0 auto 20px;
        width: 100%;
      }

        .info-col__img {
          max-width: 100%;
          height: auto;
        }

      .info-col__text {
        font-size: 14px;
        color: $gray;
      }

      .button {
        font-size: 16px;
        line-height: 50px;
        display: inline-block;
        padding: 0 25px;
        height: 50px;
        background-color: $blue;
        color: #fff;
        text-align: center;
        text-transform: uppercase;
        transition: all .5s;
        margin-top: 10px;
        font-weight: bold;

        &:hover {
          background-color: $blue3;
        }
      }

        .info-col__list-item {
          height: 50px;
          width: 100%;
          border-bottom: 1px solid rgba($black, .1);

          &:last-child {
            border-bottom: none;
          }

          &:hover {

            .fa,
            .info-col__list-item-link {
              color: $black;
            }
          }

          .fa {
            font-size: 22px;
            color: $blue;
            line-height: 50px;
            margin-right: 10px;
            transition: all .5s;
          }
        }

          .info-col__list-item-link {
            color: $blue;
            font-size: 16px;
            line-height: 50px;
            transition: all .5s;
          }

      .info-center {
        background-color: $blue;
        padding: 30px 30px 20px 30px;
        margin-bottom: 30px;

        .info-col__title {
          color: #fff;
          border-bottom: 1px solid #fff;
          line-height: 50px;
          margin-bottom: 0;
          padding-bottom: 10px;

          .fa {
            margin-right: 10px;
            font-size: 50px;
            line-height: 50px;
          }
        }
      }  
      
        .info-col__working-hours {
          padding: 25px 0;
          border-bottom: 1px solid #fff;
        }

          .info-center__title {
            font-size: 24px;
            color: #fff;
          }

          .info-center__text {
            font-size: 14px;
            color: #fff;
          }

        .info-col__phone {
          padding-top: 25px;
        }

          .info-center__text_16px {
            font-size: 16px;
            margin-top: 10px;
            display: inline-block;
          }

      .reviews {
        height: 250px;
        width: 100%;
        background-color: $light-gray;
        padding: 30px 30px 10px 30px;
      }

        .reviews-container {
          overflow: hidden;
          height: 210px;
        }

          .reviews-list {
            white-space: nowrap;
            height: 190px;
            position: relative;
            font-size: 0;
            left: 0;
            transition: all 1s;
          }

            .reviews-item {
              height: 190px;
              width: 100%;
              display: inline-block;
            }

             .reviews-item__pic {
                display: inline-block;
                margin-right: 30px;
                height: 80px;
                width: 80px;
                vertical-align: top;
             }

            .reviews-item__info {
              width: 190px;
              display: inline-block;
              vertical-align: top;
            }

              .reviews-item__info-text {
                font-size: 14px;
                color: $gray;
                white-space: normal;
                vertical-align: middle;

                .fa {
                  font-size: 34px;
                  color: $blue;
                }
              }

              .reviews-item__info-link {
                font-size: 16px;
                color: $blue;
                line-height: 50px;

                &:hover {
                  text-decoration: underline;
                }
              }

          .reviews-control {
            text-align: center;
          }

            .reviews-control__button {
              display: inline-block;
              width: 14px;
              height: 14px;
              border-radius: 50%;
              background-color: #dbdbdb;
              margin-right: 10px;
              cursor: pointer;

              &:last-child {
                margin-right: 0;
              }

              &.active-review {
                background-color: $blue;
              }

              &:hover {
                background-color: $blue;
              }
            }

.contacts {
    font-size: 0;
    padding-top: 30px;
    background-color: $black;

    .container {
      border-bottom: 1px solid $gray;
    }
  }

  .contacts-container {
    margin-left: -30px;
  }

    .contacts__item {
      width: calc(33% - 30px);
      margin-left: 30px;
      display: inline-block;
      margin-bottom: 30px;

      &:hover {
        .contacts__item-text {
          color: $blue;
        }
      }
    }

      .contacts__item-icon {
        width: 70px;
        height: 70px;
        background-color: #4a4c4d;
        color: $blue;
        font-size: 34px;
        line-height: 70px;
        text-align: center;
        display: inline-block;
        vertical-align: middle;
        margin-right: 30px;
      }

      .contacts__item-text {
        display: inline-block;
        width: 250px;
        vertical-align: middle;
        font-size: 16px;
        color: $gray;
        white-space: pre-line;
      }

      .contacts__item-text_24px {
        font-size: 24px;
      }