.questions {

}

  .questions-answers {
    margin-top: 40px;
    padding-bottom: 40px;
    position: relative;

    .main__text {
      margin-bottom: 0;
    }

    &:after {
      display: block;
      content: '';
      left: 0;
      right: 0;
      bottom: 0;
      height: 1px;
      position: absolute;
      background-color: rgba($black, .1);
    }

    &:last-child {
      margin-bottom: 0;

      &:after {
        display: none;
      }
    }
  }

    .questions-answers__item {
      display: inline-block;
      width: calc(25% - 4px);
    }

      .questions-answers__item-town {
        color: $blue;
        font-size: 24px;
      }

      .questions-answers__item-number {
        color: $black;
        font-size: 24px;
        transition: all .5s;

        &:hover {
          color: $blue;
        }
      }

      .questions-answers__item-time {
        color: $gray;
        font-size: 16px;
      }

    .button_gray {
      background-color: $light-gray;
      color: $blue;
      transition: all .5s;
      margin-right: 6px;

      &:hover {
        background-color: $blue1;
        color: #fff;
      }
    }

.faqs-awards {
  background-color: $light-gray;
  margin-top: 0;

  .awards-item {
    

    &:hover {

      .awards-icon {
        background-color: $blue1;
      }
    }
  }

    .main__text_title {
      color: $black;
      transition: all .5s;

      &:hover {
        color: $blue;
      }
    }

    .main__text {
      color: $gray;
    }

    .awards-icon {
      background-color: $blue;
      transition: all .5s;

      .fa {
        color: #fff;
      }
    }
}

.add-info {
  margin: 40px 0 60px;
}

  .add-info__menu {
    margin-bottom: 40px;
  }

    .add-info__menu-pic {
      width: 370px;
      height: 350px;
      display: inline-block;
      background: url('../img/faqs/add-info.jpg') center no-repeat;
      background-size: cover;
      vertical-align: top;
      margin-right: 30px;
    }

    .add-info__menu-list {
      display: inline-block;
      vertical-align: top;
    }

  .main__text_title {
    color: $blue;
  }