* {
  box-sizing: border-box;
}

html, body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

body {
  font: 400 14px/28px "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
}

ul {
  margin: 0;
  padding: 0;

  li {
    list-style: none;
  }
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-weight: normal;
}

a {
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;

}

textarea, 
input, 
button, 
select { 
  font-family: inherit; font-size: inherit; 
}

.wrapper {
  min-height: 100%;
  min-width: $container-width + 20px;
  overflow: hidden;

  &:after {
    content: "";
    display: block;
    position: relative;
    height: 105px;
  }
}

.container {
  margin: 0 auto;
  width: $container-width;
  position: relative;
}