.best-services {
  margin: 20px 0 50px;
}
  
  .best-services__container {
    margin-left: -30px;
    position: relative;
    margin-bottom: 100px;

    &:after {
      display: block;
      content: '';
      left: 30px;
      right: 0;
      bottom: -50px;
      height: 1px;
      position: absolute;
      background-color: rgba($black, .1);
    }

    &:last-child {
      margin-bottom: 0;

      &:after {
        display: none;
      }
    }
  }

    .best-services__item {
      width: calc(33% - 30px);
      margin-left: 30px;
      display: inline-block;
    }

      .best-services__item-pic {
        max-width: 100%;
        width: 370px;
        height: 217px;
        background: url('../img/services/best-services-0.jpg') no-repeat center;
        background-size: cover;
        margin-bottom: 20px;
      }

.howWeHelp {
  height: 400px;
  background: url('../img/services/howWeHelp-bg.jpg') no-repeat center top;
  background-size: cover;

  .container {
    min-height: 100%;
  }
}

  .howWeHelp__block {
    width: 575px;
    position: absolute;
    right: 100px;
    top: 40px;
    @extend %clearfix;
  }

    .howWeHelp__block-info {

    }

    .howWeHelp__block-list {
      width: 270px;
    }

    .howWeHelp__block-list_left {
      float: left;
    }

    .howWeHelp__block-list_right {
      float: right;
    }

.prices {
  margin: 20px 0 50px;
}

  .prices-container {
    margin-left: -30px;
  }

    .prices-table__container {
      width: 365px;
      display: inline-block;
      margin-left: 30px;
    }

      .prices-table {
        width: 100%;
        border-collapse: collapse;
      }

        .prices-tbody {

        }

          .prices-row {
            
            &:nth-child(even) {
              background-color: $light-gray;
            }
          }

            .prices-item {
              padding: 10px;
              border: 1px solid rgba($black, .1);
              color: $gray;
            }