.facts {
  margin-top: 40px;

  .container {
    @extend %clearfix;
    border-bottom: 3px solid $blue;
    padding-bottom: 40px;
  }
}

  .facts-video {
    float: left;
    width: 50%;
  }

    .facts-video__iframe {

    }

  .facts-info {
    float: right;
    width: 50%;
    padding-left: 10px;
  }

    .main__h3 {
      font-size: 34px;
      margin-bottom: 24px;
      color: $black;
      line-height: 38px;
    }

    .facts-info__main,
    .facts-info__history {
      display: inline-block;
      width: 49%;
      vertical-align: top;
    }

      .main__text_title {
        font-size: 24px;
        color: $black;
        line-height: 38px;
        margin-bottom: 10px;
      }

      .main__text {
        color: $gray;
        margin-bottom: 20px;
      }

.whoWeAre {
  margin-top: 40px;

  .container {
    
  }
}

  .whoWeAre-container {
    @extend %clearfix;
    margin-left: -30px;
  }

    .whoWeAre__left {
      float: left;
      width: calc(50% - 30px);
      margin-left: 30px;
    }

      .whoWeAre__left-aboutUs {
        border-bottom: 1px solid rgba($black, .1);
        margin-bottom: 40px;
        padding-bottom: 20px;
      }

      .whoWeAre__left-whatWeOffer {

      }

        .whoWeAre__whatWeOffer-pic {
          margin-left: -25px;

        }

          .whoWeAre__whatWeOffer-img {
            width: 270px;
            height: 270px;
            margin-left: 25px;
          }

    .whoWeAre__right {
      float: right;
      width: calc(50% - 30px);
      margin-left: 30px;
    }

      .whoWeAre__right-stuff {
        border-bottom: 1px solid rgba($black, .1);
        margin-bottom: 40px;
        padding-bottom: 20px;
      }

        .whoWeAre__stuff-pic {
          margin-left: -25px;
        }

          .whoWeAre__stuff-img {
            width: 170px;
            height: 170px;
            margin: 0 0 17px 25px;
          }

.awards {
  background-color: $blue;
  margin-top: 40px;
  padding: 40px 0 50px;

  .container {
    @extend %clearfix;
  }

  .main__h3,
  .main__text_title,
  .main__text {
    color: #fff;
  }

  .main__text {
    margin-bottom: 0;
  }
}

  .awards-left {
    float: left;
    width: 49%;
  }

  .awards-right {
    float: right;
    width: 49%;
  }

    .awards-item {
      padding-bottom: 30px;
      margin-bottom: 30px;
      border-bottom: 1px solid rgba($black, .1);

      &:last-child {
        border-bottom: none;
        margin-bottom: 0;
        padding-bottom: 0;
      }
    }

      .awards-icon {
        width: 100px;
        height: 100px;
        background-color: #fff;
        margin-right: 25px;
        color: $blue;
        font-size: 52px;
        line-height: 100px;
        text-align: center;
        display: inline-block;
        vertical-align: middle;
      }

      .awards-info {
        display: inline-block;
        vertical-align: middle;
        width: 440px;
      }

.merit {
  margin-top: 40px;
  padding-bottom: 30px;
}

  .merit-container {
    margin-left: -50px;
  }

    .merit-item {
      display: inline-block;
      width: calc(33% - 50px);
      margin-left: 50px;
    }