.map {
  max-height: 720px;
  height: 100vh;
  background-color: $light-gray;
}

.feedback {
  margin-top: 40px
}

  .feedback-form {
    margin-bottom: 50px;
  }

    .feedback-fieldset {
      border: none;
      margin-left: -30px;
    }
      
      .feedback-item {
        display: inline-block;
        position: relative;
        width: calc(33% - 30px);
        margin-left: 30px;
      }

        .feedback-label {
          border: none;
          display: block;
          background-color: $light-gray;
          outline: none;
        }

          .feedback-input {
            border: none;
            background-color: $light-gray;
            width: 100%;
            height: 50px;
            padding: 0 15px;
            color: $gray;
            transition: all .5s;

            &::-webkit-input-placeholder       { opacity: 1; }
            &::-moz-placeholder                { opacity: 1; }
            &:-moz-placeholder                 { opacity: 1; }
            &:-ms-input-placeholder            { opacity: 1; }
            &:focus::-webkit-input-placeholder { opacity: 0; }
            &:focus::-moz-placeholder          { opacity: 0; }
            &:focus:-moz-placeholder           { opacity: 0; }
            &:focus:-ms-input-placeholder      { opacity: 0; }
          }

            .feedback-input_focused {

              +.feedback-placeholder {
                
              }
            }

          .feedback-placeholder {
            position: absolute;
            left: 15px;
            top: -2px;
            font-size: 14px;
            transition: all .5s;
            color: $gray;
            display: none;
          }
      
      .feedback-item__textarea {
        position: relative;
        outline: none;

        .feedback-placeholder {
          left: 45px;
        }

        .button {
          left: 30px;
          position: relative;
        }
      }

        .feedback-textarea {
          border: none;
          background-color: $light-gray;
          display: block;
          margin: 30px 0 20px 30px;
          width: calc(100% - 35px);
          height: 430px;
          padding: 15px;
          color: $gray;

          &::-webkit-input-placeholder       { opacity: 1; }
          &::-moz-placeholder                { opacity: 1; }
          &:-moz-placeholder                 { opacity: 1; }
          &:-ms-input-placeholder            { opacity: 1; }
          &:focus::-webkit-input-placeholder { opacity: 0; }
          &:focus::-moz-placeholder          { opacity: 0; }
          &:focus:-moz-placeholder           { opacity: 0; }
          &:focus:-ms-input-placeholder      { opacity: 0; }
        }