.slider {
  height: 444px;
  width: 100%;
  position: relative;
}

  .slider-container {
    overflow: hidden;
    height: 100%;
  }

    .slider-list {
      white-space: nowrap;
      height: 100%;
      position: relative;
      font-size: 0;
      left: 0;
      transition: all 1s;
    }

      .slider-item {
        height: 444px;
        width: 100%;
        display: inline-block;
      }

        .slider-block {
          background-repeat: no-repeat;
          background-size: cover;
          width: 100%;
          height: 100%;

          .container {
            height: 100%;
          }
        }

          .slider-block__text {
            position: absolute;
            color: $blue;
            font-size: 56px;
            line-height: 88px;
            width: 580px;
            right: 0;
            top: 50%;
            transform: translateY(-50%);
            margin-top: -20px;
            white-space: normal;
          }

  .slider-controls {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    height: 50px;
  }

    .slider-controls__button {
      font-size: 36px;
      line-height: 50px;
      color: $blue;
      width: 50px;
      display: inline-block;
      text-align: center;
      cursor: pointer;

      &.left {
        position: absolute;
        left: 20px;
      }

      &.right {
        position: absolute;
        right: 20px;
      }
    }