.header {

}

.header-top {
  height: 116px;
  padding-top: 30px;

    .container {
      @extend %clearfix;
    }
}

  .header-brand {
    float: left;
    width: 50%;
  }
    .brand-name {
      font-size: 55px;
      line-height: 55px;
      color: $blue;
      display: inline-block;
      font-weight: bold;
      margin-bottom: 0;
    }

    .brand-slogan {
      font-size: 24px;
      color: $black;
      display: inline-block;
    }

  .header-info {
    float: right;
    width: 50%;
    text-align: right;
  }

    .header-phone {
      
    }

      .phone {
        font-size: 24px;
        color: $black;
        transition: all .5s;

        &:hover {
          color: $blue;
        }

        .fa-phone {
          color: $blue;
          font-size: 34px;
          display: inline-block;
          padding-right: 10px;
        }
      }

    .header-text {
      color: $gray;
    }

.nav {
  height: 60px;
  background-color: $black;
}

  .nav-list {

  }
    
    .nav-item {
      display: inline-block;
      margin-left: -4px;
      height: 100%;
      border-right: 1px solid rgba(#fff, .1);

      &:first-child {
        border-left: 1px solid rgba(#fff, .1);
      }
    }

      .nav-item__link {
        display: inline-block;
        height: 100%;
        color: #fff;
        text-transform: uppercase;
        font-size: 16px;
        font-weight: 500;
        padding: 16px 30px;
        transition: all .5s;

        &:hover {
          background-color: $blue;
        }
      }

      .nav-item__link_active {
        background-color: $blue;
      }
